.mapsComponent{overflow:hidden}.mapsComponent__city-name{position:absolute;display:inline-block;width:100vw;padding-top:20px;font-size:28px;font-weight:bold;color:#00FFD8;text-transform:uppercase;z-index:10;text-align:center}.mapsComponent__arrows__arrow-left,.mapsComponent__arrows__arrow-right{z-index:10;position:absolute;top:42px;width:60px;height:60px;fill:#00FFD8;-webkit-transform:translateY(-50%);transform:translateY(-50%);-webkit-transition:all 0.5s;transition:all 0.5s}.mapsComponent__arrows__arrow-left.hidden,.mapsComponent__arrows__arrow-right.hidden{fill:rgba(0,255,216,0.2)}.mapsComponent__arrows__arrow-right{right:0}.mapsComponent__arrows__arrow-left{-webkit-transform-origin:50% 40%;transform-origin:50% 40%;-webkit-transform:translateY(-50%) rotate(180deg);transform:translateY(-50%) rotate(180deg)}.mapsComponent__maps{width:300vw;display:-webkit-box;display:-ms-flexbox;display:flex}.mapsComponent__maps__map{width:100vw;height:100vh}.mapsComponent__maps__map:nth-child(1){background:url("../../static/img/mapsComponent/lyon.png");background-size:cover}.mapsComponent__maps__map:nth-child(2){background:url("../../static/img/mapsComponent/paris.png");background-size:cover}.mapsComponent__maps__map:nth-child(3){background:url("../../static/img/mapsComponent/marseille.png");background-size:cover}.mapsComponent__infos{position:absolute;bottom:20px;left:5vw;width:90vw;height:70px;background-color:rgba(53,17,90,0.6);-webkit-transform:translateY(150%);transform:translateY(150%)}.mapsComponent__infos__vr,.mapsComponent__infos__share{position:absolute;height:25px;top:50%}.mapsComponent__infos__vr{left:30%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.mapsComponent__infos__share{left:70%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.mapsComponent__user-location{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:30px;height:30px;background-color:#35115A;border-radius:50%}.mapsComponent__experiences-location{position:absolute;width:25px}.mapsComponent__experiences-location#exp1{top:50%;left:80%}.mapsComponent__experiences-location#exp2{top:30%;left:10%}



